<template>
  <div>
    <!-- 头部面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片 -->
    <el-card shadow="always">
      <el-input placeholder="请输入内容" class="input-with-select">
        <el-button slot="append" icon="el-icon-search"></el-button>
      </el-input>

      <!-- 订单列表表格 -->
      <el-table :data="orderList" border style="width: 100%">
        <el-table-column type="index" lable="#"></el-table-column>
        <el-table-column prop="order_number" label="订单编号" width="500px;"></el-table-column>
        <el-table-column prop="order_price" label="订单价格" width="100px"></el-table-column>
        <el-table-column prop="order_pay" label="是否付款" width="100px">
          <template slot-scope="scope">
           <el-tag type="danger" v-if="scope.row.order_pay==0">未付款</el-tag>
           <el-tag v-else>标签一</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="是否发货" width="100px"></el-table-column>
        <el-table-column prop="update_time" label="下单时间">
          <template slot-scope="scope">
             {{scope.row.update_time | time}}
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template>
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button type="success" icon="el-icon-location-outline" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>

       <!-- 分页 -->
         <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[5,10,15,20]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
      },
      orderList: [],
      total:0
    }
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo,
      })
      console.log(res)
      if (res.meta.status != 200) return this.$message.error('订单列表请求失败')
      this.orderList = res.data.goods
      this.total=res.data.total
      console.log(this.orderList)
    },
    handleSizeChange(newPage){
      this.queryInfo.pagesize=newPage
      this.getOrderList()
    },
    handleCurrentChange(newPage){
      this.queryInfo.pagenum=newPage
      this.getOrderList()
    }
  },
  mounted() {
    this.getOrderList()
  },
  filters:{
    time(value){
      let data=new Date(value*100)
         function timeAdd0(str) {
        if (str < 10) {
          str = '0' + str;
        }
        return str
      }
        var y = data.getFullYear();
      var m = data.getMonth() + 1;
      var d = data.getDate();
      var h = data.getHours();
      var mm = data.getMinutes();
      var s = data.getSeconds();
      return y + '-' + timeAdd0(m) + '-' + timeAdd0(d) + ' ' + timeAdd0(h) + ':' + timeAdd0(mm) + ':' + timeAdd0(s);

    }
  }
}
</script>

<style>
.el-card {
  margin-top: 15px;
}
.el-input {
  width: 400px;
}
</style>